/*! Defaults End */

// =====================  modules  ========================

@import "header/header";
@import "content/content";
@import "footer/footer";

// =====================  Media  ==========================

/*! Media */
@media screen and (min-width: 1500px) {
  .content-image__block {
    width: 100%;
    height: 960px; }
  .content__propositions-text {
    width: 56%; }
  .text {
    font-size: 22px; }
  .title-main {
    font-size: 55px; }
  .categories {
    ul {
      li {
        padding: 19px 72px;
        a {
          font-size: 53px; } } } }
  .content__propositions-img {
    width: 80%;
    img {
      width: 100%; } } }
@media screen and (max-width: 1200px) {
  .header,
  .footer {
    background: #ff0022; } }
@media screen and (max-width: 992px) {
  .header,
  .footer {
    background: #fbff00; } }
@media screen and (max-width: 768px) {
  .header,
  .footer {
    background: #2bff00; } }
@media screen and (max-width: 576px) {
  .header,
  .footer {
    background: #00ccff; } }
@media screen and (max-width: 320px) {
  .header,
  .footer {
    background: #ff0080; } }
/*! Media End*/
