/*! Defaults */
body {
  color: black;
  font-family: Helvetica3;
  font-size: 1rem;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 100%;
}

.content {
  flex: 1 0 auto;
}

.footer {
  flex: 0 0 auto;
}

/*! Defaults End */
/*! .header */
.header {
  height: 80px;
  background: #232323;
  color: #fff;
}

.header__container {
  width: 100%;
}

.header__container nav {
  width: 80%;
  height: 80px;
}

.header__container nav ul {
  display: flex;
  justify-content: end;
  align-items: center;
}

.header__container nav ul li {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: 200px;
  height: 80px;
  cursor: pointer;
  transition: all linear .5s;
}

.header__container nav ul li a {
  transition: all linear .5s;
  cursor: pointer;
  font-style: normal;
  font-weight: normal;
  font-size: 21px;
  line-height: 26px;
  color: #FFFFFF;
  text-decoration: none;
  transition: all linear .5s;
  cursor: pointer;
}

.header__container nav ul li a img {
  padding: 0;
}

.header__container nav ul li a:hover {
  color: #E4D0D1;
}

.header__container nav ul li:first-child {
  width: 320px;
}

.active-color {
  background: #EF5F96;
}

.header__bottom {
  background-size: cover;
  height: 100px;
}

.header__bottom ul {
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  width: 83%;
}

.header__bottom ul li {
  position: relative;
  background: transparent;
  padding: 18px 30px;
  transition: all linear .5s;
  z-index: 999;
}

.header__bottom ul li a {
  font-style: normal;
  font-weight: normal;
  font-size: 21.875px;
  line-height: 26px;
  letter-spacing: 0.025em;
  color: #000000;
  text-decoration: none;
  z-index: 9999;
}

.header__bottom ul li:after {
  content: '';
  position: absolute;
  left: 19px;
  width: 71%;
  z-index: -1;
  height: 30px;
  background: transparent;
  transition: all linear .5s;
}

.header__bottom ul li:hover:after {
  background: #E4D0D1;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  filter: blur(10px);
}

/*! .header end*/
.content-slider {
  width: 100%;
}

.slider-main {
  width: 100%;
  min-height: 780px;
  position: relative;
}

.slider-main-item {
  height: 780px;
  background-size: cover;
}

.slider-main-item img {
  width: 100%;
  height: auto;
}

.content__categories-magazine {
  width: 100%;
  height: 843px;
  background-size: cover;
  background-position: 50% 50%;
  padding: 90px 0 47px 0;
}

.categories {
  font-family: Font;
  padding: 0 120px;
}

.categories ul {
  margin: 25px 0;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.categories ul li {
  padding: 10px 38px;
  background: #E4D0D1;
  transition: all ease-in .5s;
  cursor: pointer;
}

.categories ul li a {
  font-style: normal;
  font-weight: 500;
  font-size: 48px;
  line-height: 59px;
  letter-spacing: 0.025em;
  color: #000000;
  text-decoration: none;
}

.categories ul li:hover {
  background: #D27999;
}

.content__propositions-container {
  display: flex;
  margin: 10px 120px 46px 120px;
}

.content__propositions-text {
  background: #E4D0D1;
  padding: 27px 26px;
}

.content__propositions-text-border {
  border: 5px solid #fff;
  padding: 50px 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.content__propositions-text-border p {
  margin: 0 45px;
}

.content__propositions-text-border a {
  transition: all linear .5s;
}

.content__propositions-text-border a:hover {
  background: linear-gradient(90deg, rgba(255, 255, 255, 0.35) -7.6%, rgba(228, 208, 209, 0.35) 94.4%);
}

.title-main {
  font-family: Font;
  font-style: normal;
  font-weight: 500;
